<template>
  <el-container>
    <div class="banner">
      <el-row :gutter="10">
        <el-col :span="4" class="text_align_right margin_top5">
          <span></span>
        </el-col>
        <el-col :span="8">
          <div class="text_align_left margin_top5">
            <span></span>
            <span>|</span>
            <el-dropdown>
          <span class="el-dropdown-link">
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item></el-dropdown-item>
                <el-dropdown-item></el-dropdown-item>
                <el-dropdown-item></el-dropdown-item>
                <el-dropdown-item></el-dropdown-item>
                <el-dropdown-item></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="text_align_right margin_top5">
            <span></span>
            <span v-if="loginStatus !=1">
              <router-link :to="{path:'/'}" active-class="active" class="login_btn">
                
              </router-link>
              <span></span>
              <router-link :to="{path:'/'}" active-class="active" class="login_btn">
                
              </router-link>
            </span>
            <span v-else="loginStatus==1">
              <router-link :to="{path:'/Login'}" active-class="active" class="login_btn">
                
              </router-link>
            </span>

          </div>
        </el-col>
        <el-col :span="2">
          <div class="text_align_right margin_top5">
            
          </div>
        </el-col>
        <el-col :span="1" >
          <div @click="cart()">
            <el-popover
              placement="bottom"
              width="350"
              trigger="hover">
              <div style="overflow-y: auto;height: 250px">
                <div v-for="(o, index) in 4" :key="o">
                  <el-row>
                    <el-col :span="8">
                      <div style="width:80px;height: 80px;background-color: #F6F6F6"></div>
                    </el-col>
                    <el-col :span="8">
                      <p class="cart_order_title text_align_left">)</p>
                    </el-col>
                    <el-col :span="8">
                      <p class="cart_price">{{price}}</p>
                      <el-button type="text" class="cart_price"></el-button>
                    </el-col>
                  </el-row>
                </div>
                <el-row class="bottom_div">
                  <el-col :span="12">
                    <p>{{goodsNum}}{{goodsAmount}}</p>
                  </el-col>
                  <el-col :span="12" class="text_align_right">
                    <el-button  type="danger" class="cart_btn" @click="goCart()"></el-button>
                  </el-col>
                </el-row>

              </div>
              <div>

              </div>
              <el-button slot="reference" type="text" class="cart_link" style=""></el-button>
            </el-popover>
            <!--<router-link :to="{path:'/ShoppingCart'}" active-class="active" class="login_btn" @click="cart()">-->

            <!--</router-link>-->
            <!--<el-button type="text" @click="cart()"></el-button>-->
          </div>
        </el-col>
        <el-col :span="2">
          <div class="text_align_left margin_top5" @click="judgeParam()">
            <router-link v-if="enterpriseId === 'undefined' " :to="{path:'/PersonalCenter',query: {name: 'person'}}"
                         active-class="active" class="personal_center">
              
            </router-link>
            <router-link v-if="userId === 'undefined'" :to="{path:'/FirmCenter',query: {name: 'firm'}}"
                         active-class="active" class="personal_center">
              
            </router-link>
          </div>
        </el-col>
      </el-row>
    </div>
  </el-container>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    name: "Banner",
    data() {
      return {
        loginStatus: '0',
        userId: '',
        enterpriseId: '',
        price: '180',
        goodsNum:'2',
        goodsAmount:'256.00',
      }
    },
    created() {
      this.loginStatus = localStorage.getItem("loginStatus");
      this.enterpriseId = localStorage.getItem("enterpriseId");
      this.userId = localStorage.getItem("userId");
      console.log(' this.loginStatus==' + this.loginStatus + "enterpriseId==" + this.enterpriseId + "userId==" + this.userId)
    },
    computed: {
      ...mapGetters(['cartParams'])
    },
    methods: {
      cart() {

        // this.$router.push({path: '/ShoppingCart'});
        // this.$store.dispatch('setCartParams', 'cart');
        console.log('cartParams==' + this.cartParams);
      },
      goCart(){
        this.$router.push({path: '/ShoppingCart'});
        this.$store.dispatch('setCartParams', 'cart');
      },
      judgeParam() {
        // localStorage.setItem('center',para);
        this.$store.dispatch('setCartParams', 'center');
        console.log('cartParams11111==' + this.cartParams);
        // console.log('center=='+para);
      }
    }
  }
</script>

<style scoped>
  .banner {
    width: 100%;
    height: 2rem;
    margin-top: 0px;
    background-color: rgba(246, 246, 246, 1);
    font-size: 0.81rem;
    border-bottom: 1px solid #f2f2f2;

  }

  .banner span, div {
    color: #6C6C6C;

  }

  .cart_link {
    margin-top: -5px;
    font-size: 0.81rem;
  }
  .cart_btn {
    font-size: 14px;
    font-color:#FFFFFF;
    background-color: #DF2321;
  }
  .el-dropdown {
    font-size: 0.81rem;
  }

  .margin_top5 {
    margin-top: 5px;
  }

  .text_align_left {
    text-align: left;
  }

  .text_align_right {
    text-align: right;
  }

  .login_btn {
    color: #33B2E8;
  }

  .personal_center {
    text-decoration: none;
  }

  .cart_order_title {
    font-size: 12px;
    color: #666666;
  }

  .cart_price {
    font-size: 14px;
    color: #404040;
  }
</style>



